<template>
  <ul>
    <li v-for="item in sceneList" :key="item.designKindId">
      <router-link to="/aa">
        <img :src="'http:' + item.thumbUrl" alt="" />
        <p>{{ item.name }}</p>
      </router-link>
    </li>
  </ul>
</template>
<script>
export default {
  props: ["x"],
  data() {
    return {
      sceneList: [],
      flg:false
    };
  },
  created() {
    this.getData();
    this.flg = this.x
  },
  methods: {
    async getData() {
      var r = await fetch(
        "http://192.168.204.20:9527/mobile/main/getMainMiniProgramHomeInfo.do?_dataType=json&_dataClientType=3&_dataClientType=3&client_type=40"
      ).then((res) => res.json());
      this.sceneList = r.body.data.scene;
      this.flg = true
      
    },
  },
};
</script>
<style scoped lang="scss">
ul {
    padding: 0.2rem 0;
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-around;
  background: white;
  li {
    width: 2.2rem;
    text-align: center;

    img {
      width: 1.4rem;
      height: 1.4rem;
    }
    p {
      padding: 0.2rem;
      font-size: 0.3rem;
      color: black;
    }
  }
}
</style>