<template>
  <div class="wrap-box" ref="sc">
    
    <div class="wrap" v-for="(item, index) in list" :key="item.kindId">
      <div class="header-haibao">
        <h2>{{ item.name }}</h2>
        <p>
          <router-link to="/dd"> 进入场景 </router-link>
        </p>
      </div>

      <ul>
        <li v-for="x in item.templates" :key="x.designTemplateId" :style="{width:wid(item.name)+'rem'}">
          <router-link to="/cc">
            <div
              class="bg-wrap"
              style="
                background-image: url(https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fhbimg.b0.upaiyun.com%2Fb75fc5b61441db0de8f3caeba20275e8107d270811b329-QsLrXX_fw658&refer=http%3A%2F%2Fhbimg.b0.upaiyun.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=jpeg?sec=1620399802&t=6566604ee3886067d714dc8d479f759d);
              "
            >
              <img
                v-lazy="'https:' + x.designTemplateImageUrl+'?x-oss-process=image/resize,w_300/format,jpg'"
                :style="{
                  height: (parseInt(x.height) * wid(item.name)) / parseInt(x.width) + 'rem',
                }"
                alt=""
              />
            </div>
          </router-link>
        </li>
      </ul>

      <div class="bottom-haibao">
        <div class="more-wrap">
          <router-link to="/dd"> 更多 </router-link>
        </div>
        <div class="huan-wrap" @click="say(index)">换一批</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  // props: ["flg"],
  data() {
    return {
      list: [],
      one: 1,
      two: 1,
      three: 1,
      chuNum: 1,
      fout:1,
      five:1,
      six:1,
      sven:1,
      egi:1
    };
  },
  created() {
    this.getData();
    this.wid();
  },
  methods: {
    async getData() {
      var r = await fetch(
        "http://192.168.204.20:9527//mobile/main/getMainMiniProgramHomeInfo.do?_dataType=json&_dataClientType=3&_dataClientType=3&client_type=40"
      ).then((res) => res.json());
      this.list = r.body.data.sceneDetails.sceneDetail;
    },
    wid(item){
      // console.log(item)
      return item=='横版海报'||item=='名片'?4.2:3
      
    },

    async say(index) {
      console.log(this.list)
      if (index == 0) {
        if (this.flg) {
          this.flg = false;
          this.one++;
          var res = await fetch(
            "http://192.168.204.20:9527/pub/cache/today_recommend_template_v2/today_recommend_mobile_v2_" +
              this.list[0].kindId +
              "_6_" +
              this.one +
              "?v=1617802285000&_dataClientType=3&client_type=40"
          );
          var res1 = await res.json();
          this.list[0].templates = res1.body.templates;
        }
      } else if (index == 1) {
        if (this.flg) {
          this.two++;
          console.log(this.list[1].kindId);

          var r1 = await fetch(
            "http://192.168.204.20:9527/pub/cache/today_recommend_template_v2/today_recommend_mobile_v2_" +
              this.list[1].kindId +
              "_6_" +
              this.two +
              "?v=1617804082000&_dataClientType=3&client_type=40"
          );

          var r2 = await r1.json();
          this.list[1].templates = r2.body.templates;
        }
      } else if (index == 2) {
        if (this.flg) {
          console.log(this.list[2].kindId);
          this.three++;
          var rr1 = await fetch(
            "http://192.168.204.20:9527/pub/cache/today_recommend_template_v2/today_recommend_mobile_v2_" +
              this.list[2].kindId +
              "_6_" +
              this.three +
              "?v=1617804082000&_dataClientType=3&client_type=40"
          );
          var rr2 = await rr1.json();
          this.list[2].templates = rr2.body.templates;
        }
      }else if(index == 3){
           if (this.flg) {
          console.log(this.list[3].kindId);
          this.fout++;
          var rrr1 = await fetch(
            "http://192.168.204.20:9527/pub/cache/today_recommend_template_v2/today_recommend_mobile_v2_" +
              this.list[3].kindId +
              "_6_" +
              this.fout +
              "?v=1617804082000&_dataClientType=3&client_type=40"
          );
          var rrr2 = await rrr1.json();
          this.list[3].templates = rrr2.body.templates;
        }

      }else if(index == 4){
           if (this.flg) {
          console.log(this.list[4].kindId);
          this.five++;
          var res2 = await fetch(
            "http://192.168.204.20:9527/pub/cache/today_recommend_template_v2/today_recommend_mobile_v2_" +
              this.list[4].kindId +
              "_6_" +
              this.five +
              "?v=1617804082000&_dataClientType=3&client_type=40"
          );
          var res3 = await res2.json();
          this.list[4].templates = res3.body.templates;
        }

      }else if(index == 5){
           if (this.flg) {
          console.log(this.list[5].kindId);
          this.six++;
          var res5 = await fetch(
            "http://192.168.204.20:9527/pub/cache/today_recommend_template_v2/today_recommend_mobile_v2_" +
              this.list[5].kindId +
              "_6_" +
              this.six +
              "?v=1617804082000&_dataClientType=3&client_type=40"
          );
          var res15 = await res5.json();
          this.list[5].templates = res15.body.templates;
        }

      }else if(index == 6){
           if (this.flg) {
          console.log(this.list[6].kindId);
          this.sven++;
          var res6 = await fetch(
            "http://192.168.204.20:9527/pub/cache/today_recommend_template_v2/today_recommend_mobile_v2_" +
              this.list[6].kindId +
              "_6_" +
              this.sven +
              "?v=1617804082000&_dataClientType=3&client_type=40"
          );
          var res61 = await res6.json();
          this.list[6].templates = res61.body.templates;
        }

      }else if(index == 7){
           if (this.flg) {
          console.log(this.list[7].kindId);
          this.egi++;
          var res7 = await fetch(
            "http://192.168.204.20:9527/pub/cache/today_recommend_template_v2/today_recommend_mobile_v2_" +
              this.list[7].kindId +
              "_6_" +
              this.egi +
              "?v=1617804082000&_dataClientType=3&client_type=40"
          );
          var res8 = await res7.json();
          this.list[7].templates = res8.body.templates;
        }

      }
    },
    async getList(url) {
      var r1 = await fetch(url).then((res) => res.json());
      console.log(r1.body.data.sceneDetail);
      var arr = r1.body.data.sceneDetail;
      for (var i = 0; i < arr.length; i++) {
        this.list.push(arr[i]);
      }
    },
    chudi(el) {
      window.addEventListener("scroll", () => {
        var offsetTop = el.offsetTop;
        var h = el.offsetHeight;
        var H = window.innerHeight;
        var scrollTop =
          document.body.scrollTop || document.documentElement.scrollTop;
        if (
          h + offsetTop - 100 < H + scrollTop &&
          h + offsetTop + 100 > H + scrollTop
        ) {
          if (this.flag) {
            this.chuNum++;
            console.log(this.chuNum)
            this.flag = false;
            console.log("触底");
            if (this.chuNum < 4) {
              this.getList(
                "http://192.168.204.20:9527/mobile/main/getSceneDetail.do?_dataType=json&_dataClientType=3&_dataClientType=3&client_type=40&page_no=" +
                  this.chuNum
              );
            } else {
              alert("到底了亲");
            }
          }
        } else {
          this.flag = true;
        }
      });
    },
  },
  watch: {
    list: {
      deep: true,
      handler() {
        this.flg = true;
        this.chudi(this.$refs.sc);
        
      },
    },
  },
};
</script>
<style scoped lang="scss">
.wrap {
  background-color: white;
  padding: 0.2rem;
  ul {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    li {
      width: 3rem;
      border: 1px solid #f4f4f4;
      box-sizing: border-box;
      margin: 0.1rem;
      .bg-wrap {
        background-position: center;
        background-size: 100%;
        background-repeat: no-repeat;
      }
      img {
        width: 100%;
      }
    }
  }
}
.header-haibao {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 0.6rem;
  h2 {
    font-size: 0.4rem;
    padding-left: 0.2rem;
    color: black;
  }
  p {
    font-size: 0.3rem;
    color: black;
  }
}
.bottom-haibao {
  display: flex;
  justify-content: space-around;
  align-items: center;
  .more-wrap {
    width: 3.4rem;
    height: 1rem;
    line-height: 1rem;
    text-align: center;
    background-color: #f3f4f9;
    border-radius: 0.5rem;
    font-size: 0.3rem;
  }
  .huan-wrap {
    width: 3.4rem;
    height: 1rem;
    line-height: 1rem;
    text-align: center;
    background-color: #f3f4f9;
    border-radius: 0.5rem;
    font-size: 0.3rem;
  }
}
</style>